___CSS_LOADER_EXPORT___.push([module.id, `.container {
    width: 100%;
    margin: 0 auto;
    padding: 2rem 3rem;
}

.header {
    margin-bottom: 2.5rem;
    padding-bottom: 1rem;
    border-bottom: 1px solid var(--sl-color-neutral-200);
}

.header h1 {
    color: var(--sl-color-neutral-900);
    font-size: 2rem;
    font-weight: 600;
    margin: 0;
}

.settings-section {
    background: var(--sl-color-neutral-0);
    border-radius: var(--sl-border-radius-medium);
    padding: 2rem 2.5rem;
    box-shadow: var(--sl-shadow-x-small);
}

.settings-section h2 {
    color: var(--sl-color-neutral-700);
    font-size: 1.25rem;
    font-weight: 600;
    margin: 0 0 1.5rem 0;
}

.settings-group {
    display: grid;
    grid-template-columns: repeat(3, 1fr);
    gap: 2rem;
}

/* Prompt 文本框占据整行 */
.settings-group sl-textarea {
    grid-column: 1 / -1;
    margin-bottom: 0.5rem;
}

/* 下拉框样式 */
.settings-group sl-select::part(combobox) {
    height: 45px;
}

/* 开关组件样式 */
sl-switch {
    --width: 52px;
    --height: 28px;
    margin-bottom: 1rem;
}

/* 按钮组样式 */
.button-group {
    margin-top: 3rem;
    padding-top: 1.5rem;
    border-top: 1px solid var(--sl-color-neutral-200);
    display: flex;
    justify-content: flex-end;
    gap: 1.5rem;
}

.button-group sl-button::part(base) {
    font-size: 1rem;
    font-weight: 500;
    padding: 0 2rem;
    height: 45px;
}

/* 美化滚动条 */
::-webkit-scrollbar {
    width: 10px;
}

::-webkit-scrollbar-track {
    background: var(--sl-color-neutral-100);
    border-radius: 5px;
}

::-webkit-scrollbar-thumb {
    background: var(--sl-color-neutral-300);
    border-radius: 5px;
}

::-webkit-scrollbar-thumb:hover {
    background: var(--sl-color-neutral-400);
}

.excluded-domains {
    margin-top: 1rem;
}

.excluded-domains-list {
    display: flex;
    flex-wrap: wrap;
    gap: 0.5rem;
    margin-top: 0.5rem;
    min-height: 2rem;
    padding: 0.5rem;
    border: 1px solid var(--sl-color-neutral-200);
    border-radius: var(--sl-border-radius-medium);
}

sl-tag {
    --sl-color-neutral-600: var(--sl-color-primary-600);
}`, "",{"version":3,"sources":["webpack://./src/pages/settings/style.css"],"names":[],"mappings":"AAAA;IACI,WAAW;IACX,cAAc;IACd,kBAAkB;AACtB;;AAEA;IACI,qBAAqB;IACrB,oBAAoB;IACpB,oDAAoD;AACxD;;AAEA;IACI,kCAAkC;IAClC,eAAe;IACf,gBAAgB;IAChB,SAAS;AACb;;AAEA;IACI,qCAAqC;IACrC,6CAA6C;IAC7C,oBAAoB;IACpB,oCAAoC;AACxC;;AAEA;IACI,kCAAkC;IAClC,kBAAkB;IAClB,gBAAgB;IAChB,oBAAoB;AACxB;;AAEA;IACI,aAAa;IACb,qCAAqC;IACrC,SAAS;AACb;;AAEA,mBAAmB;AACnB;IACI,mBAAmB;IACnB,qBAAqB;AACzB;;AAEA,UAAU;AACV;IACI,YAAY;AAChB;;AAEA,WAAW;AACX;IACI,aAAa;IACb,cAAc;IACd,mBAAmB;AACvB;;AAEA,UAAU;AACV;IACI,gBAAgB;IAChB,mBAAmB;IACnB,iDAAiD;IACjD,aAAa;IACb,yBAAyB;IACzB,WAAW;AACf;;AAEA;IACI,eAAe;IACf,gBAAgB;IAChB,eAAe;IACf,YAAY;AAChB;;AAEA,UAAU;AACV;IACI,WAAW;AACf;;AAEA;IACI,uCAAuC;IACvC,kBAAkB;AACtB;;AAEA;IACI,uCAAuC;IACvC,kBAAkB;AACtB;;AAEA;IACI,uCAAuC;AAC3C;;AAEA;IACI,gBAAgB;AACpB;;AAEA;IACI,aAAa;IACb,eAAe;IACf,WAAW;IACX,kBAAkB;IAClB,gBAAgB;IAChB,eAAe;IACf,6CAA6C;IAC7C,6CAA6C;AACjD;;AAEA;IACI,mDAAmD;AACvD","sourcesContent":[".container {\n    width: 100%;\n    margin: 0 auto;\n    padding: 2rem 3rem;\n}\n\n.header {\n    margin-bottom: 2.5rem;\n    padding-bottom: 1rem;\n    border-bottom: 1px solid var(--sl-color-neutral-200);\n}\n\n.header h1 {\n    color: var(--sl-color-neutral-900);\n    font-size: 2rem;\n    font-weight: 600;\n    margin: 0;\n}\n\n.settings-section {\n    background: var(--sl-color-neutral-0);\n    border-radius: var(--sl-border-radius-medium);\n    padding: 2rem 2.5rem;\n    box-shadow: var(--sl-shadow-x-small);\n}\n\n.settings-section h2 {\n    color: var(--sl-color-neutral-700);\n    font-size: 1.25rem;\n    font-weight: 600;\n    margin: 0 0 1.5rem 0;\n}\n\n.settings-group {\n    display: grid;\n    grid-template-columns: repeat(3, 1fr);\n    gap: 2rem;\n}\n\n/* Prompt 文本框占据整行 */\n.settings-group sl-textarea {\n    grid-column: 1 / -1;\n    margin-bottom: 0.5rem;\n}\n\n/* 下拉框样式 */\n.settings-group sl-select::part(combobox) {\n    height: 45px;\n}\n\n/* 开关组件样式 */\nsl-switch {\n  